
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3a50;
  text-align: center;
  margin-top: 60px;
}
.app-title {
  font-size: 35px;
  margin-bottom: 40px;
}

section {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
}
.periodic_table {
   color: #fff !important;
   font-weight: bold;
   font-size: 15px;
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
}
.periodic_table + .periodic_table {
   margin-left: 7px;
}
.periodic_table.color-1 {
   background: #00942d;
}
.periodic_table.color-2 {
    background: rgb(0, 206, 188);
}
.periodic_table.color-3 {
    background: rgb(234, 190, 8);
}

.download-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-width: 46px;
  max-width: 320px;
  min-height: 46px;
  border-radius: 3px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border: none;
  text-decoration: none;
  color: inherit;
}
